.hero {
  height: 100vh;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $primary;

    z-index: 2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("/assets/img/hero-pattern.svg");
    background-size: 120%;
    background-position: center center;
    z-index: 3;
  }

  &__content {
    z-index: 5;
    position: relative;
    color: $secondary;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
  }

  &__title {
    // color: $secondary;
    font-size: 60px;
  }
  &__subtitle {
    max-width: 70%;
    margin: 0 auto;
    font-size: 22px;
  }
}

.about {
  height: 100vh;
  background-image: url("/assets/img/about-pattern.svg");
}

